      showDiscount : true,
      누른거 : 0,
      모달창열렸니 : false,
      productsOriginal : [...oneroom],
      products : oneroom,
      메뉴들 : ['Home','Shop','About'],
      신고수 : 0

    }
  },
  methods : {
   increase(){
    this.신고수 += 1;
   },
   priceSort(){
     this.products.sort(function(a,b){
       return a.price -b.price
     })
   },
   sortBack(){
    this.products = this.productsOriginal;
   },
  beforeMount() {
    
  },
  
  }

}
