<template>
    <div class="black-bg" v-if="모달창열렸니==true">
    <div class="white-bg">
      
      <img :src="products[누른거].image" style="width:100%">
      <h4>{{products[누른거].title}}</h4>
      <p>{{products[누른거].content }}</p>
      <input v-model="month" />
      
      <p> {{month}}개월 선택함 : {{products[누른거].price * month}} 원</p>
      <Discount/>
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
    name : 'Modal',
    beforeUpdate() {
      if(this.month == 2){
        alert('2개월은 너무 적소 4달라 합시다.')
        this.month = 4;
      }
    },
    data(){
      return {
        month : 1,
      }
    },
    watch : {
      month(a){
        if(a >= 13){
          alert('13이상 입력하지마.')
        }else if(isNaN(a) == true){
          alert('문자입력하지마');
          this.month = 1;
        }
      }
    },
    props : {
        products : Array,
        누른거 : Number,
        모달창열렸니 : Boolean,
    }
}
</script>

